.c-footer {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 672px;
  padding: 80px 120px;
  background: url(../../../../../images/ui-kit/footers/footer7/back.jpg) no-repeat;
  background-size: cover; }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c-footer {
      padding: 40px; } }
  @media only screen and (min-width: 426px) and (max-width: 768px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: center;
      height: 1250px;
      padding: 40px; } }
  @media only screen and (max-width: 425px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: center;
      height: 1850px;
      padding: 20px; } }
  .c-footer__logo {
    width: 150px; }
  .c-footer__menu-item {
    width: 150px; }
    @media only screen and (min-width: 426px) and (max-width: 768px) {
      .c-footer__menu-item {
        width: 200px;
        margin-bottom: 20px; } }
  .c-footer__menu-subitem {
    display: block;
    outline: none;
    color: #9d9d9e;
    font-size: 16px;
    line-height: 2;
    letter-spacing: 0.02rem;
    text-decoration: none; }
    .c-footer__menu-subitem:first-child {
      color: #fff;
      line-height: 2.25;
      letter-spacing: 0.05rem;
      text-transform: uppercase; }
      .c-footer__menu-subitem:first-child:hover {
        text-decoration: none; }
    .c-footer__menu-subitem:hover {
      text-decoration: underline; }
    .c-footer__menu-subitem.new {
      position: relative; }
      .c-footer__menu-subitem.new::after {
        content: 'NEW';
        position: absolute;
        margin-left: 10px;
        padding: 5px;
        border: 1px solid transparent;
        border-radius: 6px;
        background-color: #d9c26c;
        color: #383737;
        font-size: 14px;
        line-height: 1.25;
        letter-spacing: 0.05rem;
        text-align: center; }
      .c-footer__menu-subitem.new:hover::after {
