  .c-footer__other {
    width: 260px; }
    @media only screen and (min-width: 426px) and (max-width: 768px) {
      .c-footer__other {
        width: 360px;
        text-align: center; } }
  .c-footer .c-social-title {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    font-size: 16px;
    line-height: 2.25;
    letter-spacing: 0.05rem;
    text-transform: uppercase; }
  .c-footer .c-social-item {
    padding: 9px;
    border: 1px solid;
    border-radius: 6px;
    color: #fff;
    font-size: 14px;
    text-decoration: none; }
    @media only screen and (min-width: 769px) and (max-width: 1024px) {
      .c-footer .c-social-item {
        margin-bottom: 10px; } }
    .c-footer .c-social-item:not(:last-child) {
      margin-right: 10px; }
    .c-footer .c-social-item.c--facebook {
      border-color: #3162a2;
      background-color: #3162a2; }
      .c-footer .c-social-item.c--facebook:hover {
        border-color: #235493;
        background-color: #235493;
        cursor: pointer; }
    .c-footer .c-social-item.c--twitter {
      border-color: #2eb1eb;
      background-color: #2eb1eb; }
      .c-footer .c-social-item.c--twitter:hover {
        border-color: #209dd4;
        background-color: #209dd4;
        cursor: pointer; }
    .c-footer .c-social-item.c--dribbble {
      border-color: #f552b6;
      background-color: #f552b6; }
      .c-footer .c-social-item.c--dribbble:hover {
        border-color: #d43b99;
        background-color: #d43b99;
        cursor: pointer; }
    .c-footer .c-social-item.c--vimeo {
      border-color: #3498b9;
      background-color: #3498b9; }
      .c-footer .c-social-item.c--vimeo:hover {
        border-color: #2485a5;
        background-color: #2485a5;
        cursor: pointer; }
    .c-footer .c-social-item.c--google-plus {
      border-color: #f25c58;
      background-color: #f25c58; }
      .c-footer .c-social-item.c--google-plus:hover {
