  .c-footer__copyright {
    position: absolute;
    right: 120px;
    bottom: 30px;
    color: #9d9d9e;
    font-size: 14px;
    line-height: 2.57;
    letter-spacing: 0.02rem; }
    @media only screen and (min-width: 426px) and (max-width: 768px) {
      .c-footer__copyright {
