        line-height: 1.25;
        text-align: center;
        transform: translateX(-50%); } }

.c__subscribe-box {
  margin-top: 50px;
  margin-bottom: 40px; }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c__subscribe-box {
      margin-top: 20px; } }
  .c__subscribe-box-title {
    display: block;
    color: #fff;
    font-size: 16px;
    line-height: 2.25;
    letter-spacing: 0.05rem;
    text-transform: uppercase; }
  .c__subscribe-box-text {
    display: block;
    margin-bottom: 15px;
    color: #9d9d9e;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.02rem; }
  .c__subscribe-box-input-wrapper {
    display: inline-block;
    width: 202px;
    height: 36px;
    margin-right: 10px; }
  .c__subscribe-box-input {
    width: 100%;
    height: 100%;
    padding: 10px;
    border: 1px solid transparent;
    border-radius: 6px;
    background-color: rgba(255, 255, 255, 0.25);
    outline: none;
    color: #fff;
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.02rem; }
    .c__subscribe-box-input:focus {
      background-color: rgba(255, 255, 255, 0.4); }
    .c__subscribe-box-input::placeholder {
      color: #fff; }
  .c__subscribe-box-submit-wrapper {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 36px;
    vertical-align: bottom; }
    .c__subscribe-box-submit-wrapper::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 5px;
      height: 5px;
      border-top: solid 2px #fff;
      border-right: solid 2px #fff;
      transform: translate(-50%, -50%) rotate(45deg);
      transform-origin: center;
      pointer-events: none; }
  .c__subscribe-box-submit {
    width: 100%;
    height: 100%;
    border: 1px solid transparent;
    border-radius: 6px;
    background-color: #24aa98;
    outline: none; }
    .c__subscribe-box-submit:hover {
      background-color: #1a9385;
      cursor: pointer; }
